import React, { useContext, useState, useEffect } from "react";
import ElementosGastados from "./ElementosGastados";
import { AppContext } from "../context/AppContext";
import { motion } from "framer-motion";

const ListaGastos = () => {
  const { expenses } = useContext(AppContext);
  const [filteredExpenses, setFilteredExpenses] = useState(expenses || []);

  useEffect(() => {
    setFilteredExpenses(expenses);
  }, [expenses]);

  const handleChange = (event) => {
    const searchResults = expenses.filter(
      (filteredExpenses) =>
        filteredExpenses.name.toLowerCase().includes(event.target.value) ||
        filteredExpenses.name.toUpperCase().includes(event.target.value) ||
        filteredExpenses.name.includes(event.target.value)
    );
    setFilteredExpenses(searchResults);
  };

  return (
    <>
      <input
        type="text"
        class="form-control mb-2 mr-sm-2"
        placeholder="Buscar..."
        onChange={handleChange}
      />
      <ul class="list-group mt-3 mb-3">
        {filteredExpenses.map((expense) => (
          <ElementosGastados
            id={expense.id}
            name={expense.name}
            cost={expense.cost}
          />
        ))}
      </ul>
    </>
  );
};

export default ListaGastos;
