import React, { useState, useContext } from "react";
import VerPresupuesto from "./VerPresupuesto";
import EditarPresupuesto from "./EditarPresupuesto";
import { AppContext } from "../context/AppContext";
import { motion } from "framer-motion";

const Presupuesto = () => {
  const { budget, dispatch } = useContext(AppContext);
  const [isEditing, setIsEditing] = useState(false);

  const handleEditClick = () => {
    setIsEditing(true);
  };

  const handleSaveClick = (value) => {
    dispatch({
      type: "SET_BUDGET",
      payload: value,
    });
    setIsEditing(false);
  };

  return (
    <motion.div
      class="alert alert-secondary p-3 d-flex align-items-center justify-content-between"
      whileHover={{ scale: 1.05, boxShadow: "0px 0px 8px #d3d6d8" }}
      whileTap={{ scale: 0.9 }}
      transition={{ type: "spring", stiffness: 300 }}
      drag
    >
      {isEditing ? (
        <EditarPresupuesto handleSaveClick={handleSaveClick} budget={budget} />
      ) : (
        <VerPresupuesto handleEditClick={handleEditClick} budget={budget} />
      )}
    </motion.div>
  );
};

export default Presupuesto;
