import React from "react";

const VerPresupuesto = (props) => {
  return (
    <>
      <span>Presupuesto: ${props.budget}</span>
      <button
        type="button"
        class="btn btn-outline-primary btn-sm"
        onClick={props.handleEditClick}
      >
        Cambiar
      </button>
    </>
  );
};

export default VerPresupuesto;
