import { useReducer } from "react";
import { createContext } from "react";

// El reductor se encarga de crear el nuevo objeto de estado global, basado en un tipo
// de acción y una carga útil.
const AppReducer = (state, action) => {
  switch (action.type) {
    case "ADD_EXPENSE":
      return {
        ...state,
        expenses: [...state.expenses, action.payload],
      };
    case "DELETE_EXPENSE":
      return {
        ...state,
        expenses: state.expenses.filter(
          (expense) => expense.id !== action.payload
        ),
      };
    case "SET_BUDGET":
      return {
        ...state,
        budget: action.payload,
      };
    default:
      return state;
  }
};

// Ésta API es lo que se usará para almacenar nuestro estado global.
const initialState = {
  budget: 2000,
  expenses: [
    { id: 12, name: "Shopping", cost: 40 },
    { id: 13, name: "Vacaciones", cost: 400 },
    { id: 14, name: "Mantenimiento de Auto", cost: 50 },
  ],
};

export const AppContext = createContext();

// El AppProvider es un componente que envuelve los componentes a los que queremos pasar el estado.
export const AppProvider = (props) => {
  const [state, dispatch] = useReducer(AppReducer, initialState);

  /**Devolvemos AppContext.Provider.
   * Esto tiene una propuesta de valor que contiene
   * los datos que permitimos que nuestros componentes vean y tengan acceso */
  return (
    <AppContext.Provider
      value={{
        budget: state.budget,
        expenses: state.expenses,
        dispatch,
      }}
    >
      {props.children}
    </AppContext.Provider>
  );
};
