/**
 * * AñadirGasto.js
 * ? Componente formulario para añadir un gasto
 * * Brizuela González Juan Eduardo
 */
import React, { useContext, useState } from "react";
import { AppContext } from "../context/AppContext";
import { v4 as uuidv4 } from "uuid"; // ids
import { motion } from "framer-motion";

const AñadirGasto = () => {
  const { dispatch } = useContext(AppContext);
  const [name, setName] = useState("");
  const [cost, setCost] = useState("");

  const onSubmit = (event) => {
    event.preventDefault();

    // creación de objeto "expense"
    const expense = {
      // usando el paquete uuid importado, se crea un ID.
      // esto se usa para identificar un gasto dado
      id: uuidv4(),
      name: name,
      cost: parseInt(cost),
    };

    dispatch({
      type: "ADD_EXPENSE",
      payload: expense,
    });
  };

  return (
    <form onSubmit={onSubmit}>
      <div className="row">
        <div className="col-sm">
          <label for="name">Descripción</label>
          <input
            required="required"
            type="text"
            className="form-control"
            id="name"
            value={name}
            onChange={(event) => setName(event.target.value)}
          ></input>
        </div>
        <div className="col-sm">
          <label for="cost">Cantidad Gastada</label>
          <input
            required="required"
            type="text"
            className="form-control"
            id="cost"
            value={cost}
            onChange={(event) => setCost(event.target.value)}
          ></input>
        </div>
        <div className="col-sm">
          <motion.button
            type="submit"
            className="btn btn-primary mt-4"
            whileHover={{ scale: 1.1 }}
            transition={{ type: "spring", stiffness: 600 }}
            whileTap={{ scale: 0.9 }}
          >
            Guardar
          </motion.button>
        </div>
      </div>
    </form>
  );
};

export default AñadirGasto;
