import { motion } from "framer-motion";
import React from "react";

const UserNav = () => {
  return (
    <nav class="navbar navbar-expand-xxl navbar-light new-navbar">
      <a class="navbar-brand">
        <img class="logo" src="./ficon.png" width="30" alt="Finance Logo" />
      </a>
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarNavAltMarkup"
        aria-controls="navbarNavAltMarkup"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
        <div class="navbar-nav">
          <a class="nav-link" href="planes.html">
            Planes
          </a>
          <a class="nav-link" href="acerca.html">
            Acerca de
          </a>
          <a class="nav-link" href="contacto.html">
            Contacto
          </a>
        </div>
      </div>
      <motion.a whileHover={{ scale: 1.1 }}>
        Es un placer tenerte de vuelta,{" "}
        <motion.a
          animate={{
            originX: 0,
            color: "#0d6efd",
            textShadow: "0px 0px 20px #0d6efd",
          }}
          transition={{ yoyo: 1, duration: 2 }}
        >
          Juan.
        </motion.a>
      </motion.a>
    </nav>
  );
};

export default UserNav;
